.post {
  /* position: relative; */
  width: 385px;
  flex: 0 0 auto;
  display: flex;
  flex-direction: column;
  overflow: hidden;

  /* animation: drac 1s ease-in-out forwards;  */
  background-color: #302f2f;
  ;
  border-radius: 10px;
}

.post:hover {
  box-shadow: inset 0 0 2px 2px rgba(255, 255, 255, 0.31);
  transform: scale(1.1);
}

.like-dislike-buttons {

  display: flex;
}

.r-m-b {

  color: #1fd8c9;


}

.r-m-b:hover {
  color: var(--primaryColor);
}

.fin {
  display: flex;
  justify-content: space-between;

  margin: 5px;
  margin-top: auto;

}

.like-dislike-buttons span {

  margin-right: 5px;
  cursor: pointer;
  /* transition: transform 0.2s ease-in-out; */
  color: white;
  z-index: 10;
  transition: opacity 0.2s ease-in-out, transform 0.2s ease-in-out;

}

.like-dislike-buttons span.clicked {
  opacity: 0.5;
  /* Adjust the opacity as needed */
  border: 2px solid white;
}

.like-dislike-buttons span:hover {
  transform: scale(1.2);

}

.like-count,
.dislike-count {
  margin-right: 10px;
  color: white;
}


.postImg {
  position: relative;
  width: 100%;
  height: 280px;
  object-fit: cover;
  border-radius: 7px;
  position: relative;
  /* Add this line */
}

.postInfo {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: goldenrod;
}

.postCat {
  font-family: "Varela Round", Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 11px;
  color: #be9656;
  line-height: 19px;
  margin-top: 15px;
  margin-right: 10px;
  cursor: pointer;
}

.love-count.clicked {
  color: rgb(255, 0, 55);
  /* Change to your desired color */
}

.singlBnn img {
  height: 60vh;
}

.postTitle {
  /* font-family: "Josefin Sans", Arial, Helvetica, sans-serif; */
  font-size: 20px;
  font-weight: 500;
  margin-top: 10px;
  color: var(--primaryColor);
  cursor: pointer;


}

.postDate {
  font-family: "Lora", serif;
  font-style: italic;
  font-size: 13px;
  font-weight: 400;
  color: #1fd8c9;
}

.postDesc {
  font-family: "Varela Round", Arial, Helvetica, sans-serif;
  font-weight: 400;
  color: var(--white);
  /* margin-top: 10px; */
  margin: 10px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  /* -webkit-line-clamp: 4; */
  -webkit-box-orient: vertical;
}


@media screen and (max-width: 768px) {

  .post {
    /* position: relative; */
    width: 100%;
    flex: 0 0 auto;
    display: flex;
    flex-direction: column;
    overflow: hidden;

    /* animation: drac 1s ease-in-out forwards;  */
    background-color: #302f2f;
    ;
    border-radius: 10px;
  }

}