
.singlePost {
    flex: 1;
    padding: 2% 40% 3% 2%;
    background-color: var(--abg);
  }
  
  .singlePostWrapper {
    padding: 20px;
    padding-right: 0;
  }
  .backto{
    background-color: #5694be;
    padding: 10px;
    color: silver;
    margin-left: 10px;
  }
  .singlePostImg {
    width: 100%;
    height: 510px;
    border-radius: 5px;
    object-fit: cover;
  }
  .singlePostIcon:hover{
    transform: scale(1.1);
  }
  .singlePostTitle {
    text-align: center;
    margin: 10px;
    font-size: 28px;
    color: var(--primaryColor);
    font-family: "Lora", sans-serif;
  }
  
  .singlePostEdit {
    float: right;
    font-size: 16px;
  }
  
  .singlePostIcon {
    margin-left: 10px;
    cursor: pointer;
  }
  
  .singlePostIcon:first-child {
    color: teal;
  }
  .singlePostIcon:last-child {
    color: tomato;
  }
  
  .singlePostInfo {
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    font-size: 16px;
    color: #be9656;
    font-family: "Varela Round", Arial, Helvetica, sans-serif;
  }
  
  .singlePostAuthor{
    margin-left: 5px;
  }
  
  .singlePostDesc {
    color: var(--white);
    font-size: 18px;
    line-height: 25px;
  }
  
  .singlePostDesc::first-letter {
    margin-left: 20px;
    font-size: 30px;
    font-weight: 600;
  }

  @media screen and (max-width : 768px) {
    .singlePost {
      flex: 1;
      padding: 10px 10px 10px 10px;
      background-color: var(--abg);
    }

    .singlePostWrapper {
      padding: 3px;
      padding-right: 0;
    }
    .singlePostImg {
      width: 100%;
      height: 100%;
      border-radius: 5px;
      object-fit: cover;
    }
  }

  /* Add this CSS to your stylesheet */

.mainContainer {
  display: flex;
  justify-content: space-between;
  width: 100%;
  box-sizing: border-box;
  height: 100%;
}

.singlePostWrapper {
  width: 70%;
  padding: 20px;
  box-sizing: border-box;
}

.rightSection {
  background-color: #030916b7;
  width: 30%;
  padding: 20px;
  height: 900px; /* Adjust the height as needed */
  overflow: auto;
  box-sizing: border-box;
}


.blogSearch,
.recentBlogs {
  margin-bottom: 20px;
}

.searchContainer {
  display: flex;
  flex-direction: column;
  color: black;
  padding: 15px;
}

.searchInput,
.searchButton {
  margin-bottom: 8px;
  padding: 15px;
}

@media only screen and (max-width: 768px) {
  .mainContainer {
    flex-direction: column;
  }

  .singlePostWrapper,
  .rightSection {
    width: 100%;
    height: 100%;
  }
}