.hero-section {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
  text-align: center;
  height: 100vh;
  margin: 0%;
}

.hero-section img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

.page-turn-container {
  width: 100%;
  height: 100%;
  perspective: 1200px;
}

.page-turn {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  animation: crossfade 1s ease-in-out forwards;
}

.page-turn img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.slider-content {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;

  width: 100%;
  text-align: center;
  z-index: 200;
}

.slider-text h1 {
  color: var(--primaryColor);
  font-size: 3rem;
}

.slider-text p {
  color: var(--white);
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 8;
  background: rgba(0, 0, 0, 0.8);
}

.slider-text {
  color: white;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  display: inline-block;
  background: rgba(0, 0, 0, 0.781);
  padding: 5px;
  border-radius: 5px;
  width: 60vh;
  height: 40vh;
  box-sizing: border-box;
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.read-more-button {
  background-color: rgb(141, 121, 9);
  color: white;
  padding: 10px;
  font-size: 1rem;
  border: none;
  margin: 20px;
  cursor: pointer;
  outline: none;
  border-radius: 5px;
  margin-top: 10px;
}

.read-more-button:hover {
  box-shadow: inset 0 0 2px 2px rgb(141, 121, 9);
  transform: scale(1.1);
  background-color: var(--white);
  color: rgb(141, 121, 9);
}

.slider-buttons {
  position: absolute;
  width: 100%;
  display: flex;
  justify-content: space-between;
  bottom: 20px;
}

.arrow-button {
  background-color: transparent;
  border: none;
  color: var(--primaryColor);
  font-size: 1.5rem;
  cursor: pointer;
  outline: none;
}

@media only screen and (max-width: 768px) {

  .hero-section {
    height: 50vh;
  }

  .hero-section img {
    height: 50vh;
  }

  .slider-text {
    width: 60%;
    height: fit-content;
  }

  .slider-text h1 {
    font-size: smaller;
  }

  .slider- p {
    font-size: smaller;
  }
}

.scroll-wrapper {

  cursor: pointer;
  position: absolute;
  z-index: 9;
  top: 85%;
  left: 50%;

}


.scroll-wrapper {
  text-align: center;
}

.scroll {
  font-size: 30px;
  color: #fff;
  -webkit-animation: scrolling 3s ease infinite;
  -moz-animation: scrolling 3s ease infinite;
  -o-animation: scrolling 3s ease infinite;
  animation: scrolling 3s ease infinite;
}

@keyframes scrolling {
  0% {
    transform: translate(0);
  }

  50% {
    transform: translate(0, 40px);
  }

  100% {
    transform: translate(0);
  }
}

.hero-section {
  position: relative;

}

.slider-circles {
  position: absolute;
  bottom: 30px;
  left: 51%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
}

.circle {
  width: 17px;
  height: 17px;
  border-radius: 50%;
  border: 2px solid white;
  margin: 0 5px;
  cursor: pointer;
}

.circle.active {
  background-color: var(--primaryColor);
}